<template>
    <div class="search-status">
        <div
            v-for="(status, id) in StatusList"
            :key="id"
            class="status-item"
            @click="auditStatus(id,status.value)"
            :class="{active: activeIndex == id}"
        >{{status.label}}</div>
    </div>
</template>

<script>
export default {
    name: 'search-status',
    props: {
        StatusList: {
            type: Array,
            default() {
                return []
            },
        },
    },
    data() {
        return {
            activeIndex: null,
        }
    },
    created() {},
    mounted() {},
    methods: {
        auditStatus(index, value) {
            // 控制状态切换传值
            if (this.activeIndex != index) {
                this.activeIndex = index
                this.$emit('auditStatus', value)
            } else {
                this.activeIndex = -1
                this.$emit('auditStatus', '')
            }
        },
    },
}
</script>

<style scoped lang="scss">
.search-status {
    display: flex;
    align-items: center;
    .status-tit {
        font-size: 16px;
        font-weight: bolder;
    }
    .status-item {
        padding: 5px 10px;
        margin: 5px 10px;
        cursor: pointer;
        color: #333333;
    }
    .active {
        background-color: #f5f5f5;
    }
}
</style>
